import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Switch,
  FormControlLabel,
  Button,
  Paper,
  Grid,
  Chip,
  Alert,
  Divider
} from '@mui/material';
import { Language, Palette, Settings, Email } from '@mui/icons-material';
import { toast } from 'react-toastify';
import preferencesService from '../services/preferencesService';
import preferencesApiService from '../services/preferencesApiService';
import i18nService from '../services/i18nService';


const UserPreferences = () => {
  const [preferences, setPreferences] = useState({
    language: 'fr',
    theme: 'light',
    autoTheme: true,
    email_notifications: true
  });
  const [initialPreferences, setInitialPreferences] = useState({
    language: 'fr',
    theme: 'light',
    autoTheme: true,
    email_notifications: true
  });
  const [isLoading, setIsLoading] = useState(false);
  const [hasChanges, setHasChanges] = useState(false);
  const [forceUpdate, setForceUpdate] = useState(0);

  // Charger les prfrences au montage du composant
  useEffect(() => {
    const loadPreferences = async () => {
      try {
        const currentLanguage = i18nService.getCurrentLanguage();
        const autoTheme = preferencesService.getPreference('autoTheme') || true;
        
        // Charger les prfrences depuis l'API
        const apiPreferences = await preferencesApiService.getPreferences();
        
        const loadedPreferences = {
          language: apiPreferences?.data?.language || currentLanguage,
          theme: apiPreferences?.data?.theme || 'light',
          autoTheme,
          email_notifications: apiPreferences?.data?.email_notifications ?? true
        };
        
        setPreferences(loadedPreferences);
        setInitialPreferences(loadedPreferences);
      } catch (error) {
        console.error('Erreur lors du chargement des prfrences:', error);
        // Utiliser les valeurs par dfaut en cas d'erreur
        const currentLanguage = i18nService.getCurrentLanguage();
        const autoTheme = preferencesService.getPreference('autoTheme') || true;
        
        const defaultPreferences = {
          language: currentLanguage,
          theme: 'light',
          autoTheme,
          email_notifications: true
        };
        
        setPreferences(defaultPreferences);
        setInitialPreferences(defaultPreferences);
      }
    };

    loadPreferences();
  }, [preferences.theme]);

  // Dtecter les changements
  useEffect(() => {
    // Comparer avec les valeurs initiales charges
    const hasLanguageChanged = initialPreferences.language !== preferences.language;
    const hasThemeChanged = initialPreferences.theme !== preferences.theme;
    const hasAutoThemeChanged = initialPreferences.autoTheme !== preferences.autoTheme;
    const hasEmailNotificationsChanged = initialPreferences.email_notifications !== preferences.email_notifications;
    
    const changed = hasLanguageChanged || hasThemeChanged || hasAutoThemeChanged || hasEmailNotificationsChanged;
    setHasChanges(changed);
  }, [preferences, initialPreferences]);

  // Forcer la synchronisation des composants aprs sauvegarde
  useEffect(() => {
    if (forceUpdate > 0) {
      // Synchroniser les composants avec les nouveaux paramtres
      const currentLanguage = i18nService.getCurrentLanguage();
      
      setPreferences(prev => ({
        ...prev,
        language: currentLanguage,
        theme: prev.theme
      }));
    }
  }, [forceUpdate]);

  // Grer les changements de prfrences
  const handlePreferenceChange = (key, value) => {
    setPreferences(prev => ({
      ...prev,
      [key]: value
    }));
    
    // Marquer qu'il y a des changements
    setHasChanges(true);
  };

  // Sauvegarder les prfrences
  const handleSavePreferences = async () => {
    setIsLoading(true);
    
    try {
      // Appliquer tous les changements lors de la sauvegarde
      i18nService.setLanguage(preferences.language);
      // setTheme(preferences.theme); // DSACTIV TEMPORAIREMENT
      
      // Sauvegarder via les services locaux
      preferencesService.setPreference('autoTheme', preferences.autoTheme);
      
      // Sauvegarder les prfrences via l'API
      try {
        const apiResponse = await preferencesApiService.updatePreferences({
          email_notifications: preferences.email_notifications,
          language: preferences.language,
          theme: preferences.theme
        });
        
        // Vrifier que la rponse API est valide
        if (apiResponse && apiResponse.success) {
          console.log(' Prfrences sauvegardes avec succs via l\'API');
        } else {
          console.warn(' Rponse API inattendue:', apiResponse);
        }
      } catch (apiError) {
        console.error(' Erreur API lors de la sauvegarde des prfrences:', apiError);
        toast.error('Erreur lors de la sauvegarde sur le serveur, mais les changements locaux sont appliqus');
        // Continuer mme si l'API choue, car les changements locaux sont dj appliqus
      }
      
      // Forcer la mise  jour des composants
      setForceUpdate(prev => prev + 1);
      
      // Dclencher un vnement global pour forcer la mise  jour de tous les composants
      window.dispatchEvent(new CustomEvent('languageOrThemeChanged', {
        detail: {
          language: preferences.language,
          theme: preferences.theme,
          timestamp: Date.now()
        }
      }));
      
      toast.success(i18nService.t('success.preferencesSaved'));
      setHasChanges(false);
      // Mettre  jour les prfrences initiales aprs sauvegarde
      setInitialPreferences(preferences);
    } catch (error) {
      toast.error(i18nService.t('errors.general'));
      console.error('Erreur de sauvegarde:', error);
    } finally {
      setIsLoading(false);
    }
  };

  // Rinitialiser les prfrences
  const handleResetPreferences = () => {
    const defaultPreferences = {
      language: 'fr',
      theme: 'light',
      autoTheme: true
    };
    
    setPreferences(defaultPreferences);
    
    // Appliquer les prfrences par dfaut
    i18nService.setLanguage('fr');
    // setTheme('light'); // DSACTIV TEMPORAIREMENT
    
    setHasChanges(false);
    // Mettre  jour les prfrences initiales aprs rinitialisation
    setInitialPreferences(preferences);
  };

  return (
    <Paper sx={{ p: 3, mb: 3 }}>
      <Box display="flex" alignItems="center" mb={3}>
        <Settings sx={{ mr: 2, color: 'primary.main' }} />
        <Typography variant="h6" component="h3">
          {i18nService.t('profile.preferences.title')}
        </Typography>
      </Box>

      <Grid container spacing={3}>
        {/* Section Langue */}
        <Grid item xs={12} md={6}>
          <Box>
            <Box display="flex" alignItems="center" mb={2}>
              <Language sx={{ mr: 1, color: 'primary.main' }} />
              <Typography variant="subtitle1" fontWeight="bold">
                {i18nService.t('profile.preferences.language.title')}
              </Typography>
            </Box>
            
            <FormControl fullWidth>
              <InputLabel>
                {i18nService.t('profile.preferences.language.title')}
              </InputLabel>
              <Select
                value={preferences.language}
                onChange={(e) => handlePreferenceChange('language', e.target.value)}
                label={i18nService.t('profile.preferences.language.title')}
              >
                {i18nService.getAvailableLanguages().map((lang) => (
                  <MenuItem key={lang.code} value={lang.code}>
                    <Box display="flex" alignItems="center">
                      <span style={{ marginRight: '8px' }}>{lang.flag}</span>
                      <span>{lang.nativeName || lang.code.toUpperCase()}</span>
                    </Box>
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            

          </Box>
        </Grid>

        {/* Section Notifications Email */}
        <Grid item xs={12} md={6}>
          <Box>
            <Box display="flex" alignItems="center" mb={2}>
              <Email sx={{ mr: 1, color: 'primary.main' }} />
              <Typography variant="subtitle1" fontWeight="bold">
                {i18nService.t('profile.preferences.email.title')}
              </Typography>
            </Box>
            
            <FormControlLabel
              control={
                <Switch
                  checked={preferences.email_notifications}
                  onChange={(e) => handlePreferenceChange('email_notifications', e.target.checked)}
                  color="primary"
                />
              }
              label={
                <Box>
                  <Typography variant="body2">
                    {i18nService.t('profile.preferences.email.notifications')}
                  </Typography>
                  <Typography variant="caption" color="text.secondary">
                    {i18nService.t('profile.preferences.email.description')}
                  </Typography>
                </Box>
              }
            />
          </Box>
        </Grid>

        {/* Section Thme - DSACTIVE TEMPORAIREMENT */}
        <Grid item xs={12} md={6}>
          <Box>
            <Box display="flex" alignItems="center" mb={2}>
              <Palette sx={{ mr: 1, color: 'primary.main' }} />
              <Typography variant="subtitle1" fontWeight="bold">
                {i18nService.t('profile.preferences.theme.title')}
              </Typography>
            </Box>
            
            <Typography variant="body2" color="text.secondary" sx={{ fontStyle: 'italic' }}>
              {i18nService.t('profile.preferences.theme.disabled')}
            </Typography>
          </Box>
        </Grid>
      </Grid>

      <Divider sx={{ my: 3 }} />

      {/* Actions */}
      <Box display="flex" gap={2} justifyContent="flex-end">
        <Button
          variant="outlined"
          onClick={handleResetPreferences}
          disabled={isLoading}
        >
          {i18nService.t('common.actions.reset')}
        </Button>
        
        <Button
          variant="contained"
          onClick={handleSavePreferences}
          disabled={isLoading || !hasChanges}
          startIcon={isLoading ? null : null}
        >
          {isLoading ? i18nService.t('common.actions.saving') : i18nService.t('profile.actions.savePreferences')}
        </Button>
      </Box>

      {/* Indicateur de changements */}
      {hasChanges && (
        <Alert severity="info" sx={{ mt: 2 }}>
          {i18nService.t('profile.preferences.unsavedChanges')}
        </Alert>
      )}


    </Paper>
  );
};

export default UserPreferences;
