import authAxios from './authService';

/**
 * Service pour la gestion des prfrences utilisateur via l'API
 */
class PreferencesApiService {
  /**
   * Rcupre les prfrences de l'utilisateur connect
   */
  async getPreferences() {
    try {
      const response = await authAxios.get('/preferences');
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la rcupration des prfrences:', error);
      throw error;
    }
  }

  /**
   * Met  jour toutes les prfrences
   */
  async updatePreferences(preferences) {
    try {
      const response = await authAxios.put('/preferences', preferences);
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la mise  jour des prfrences:', error);
      throw error;
    }
  }

  /**
   * Met  jour uniquement les prfrences email
   */
  async updateEmailPreferences(email_notifications) {
    try {
      const response = await authAxios.put('/preferences/email', { email_notifications });
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la mise  jour des prfrences email:', error);
      throw error;
    }
  }
}

export default new PreferencesApiService();

