          // Couleurs de graphique
          '--chart-colors': ['#8A2BE2', '#B19CD9', '#D4C4E8', '#4B0082', '#2D004D'],
          
          // Couleurs d'accessibilit
          '--focus-ring': 'rgba(138, 43, 226, 0.25)',
          '--selection-background': 'rgba(138, 43, 226, 0.2)',
