// Service de gestion des prfrences utilisateur
class PreferencesService {
  constructor() {
    this.storageKey = 'user_preferences';
    this.defaultPreferences = {
      language: 'fr',
      theme: 'light',
      autoTheme: true
    };
    this.loadPreferences();
  }

  // Charger les prfrences depuis le localStorage
  loadPreferences() {
    try {
      const stored = localStorage.getItem(this.storageKey);
      if (stored) {
        this.preferences = { ...this.defaultPreferences, ...JSON.parse(stored) };
      } else {
        this.preferences = { ...this.defaultPreferences };
      }
    } catch (error) {
      // console.warn('Erreur lors du chargement des prfrences:', error);
      this.preferences = { ...this.defaultPreferences };
    }
  }

  // Sauvegarder les prfrences dans le localStorage
  savePreferences() {
    try {
      localStorage.setItem(this.storageKey, JSON.stringify(this.preferences));
    } catch (error) {
      // console.error('Erreur lors de la sauvegarde des prfrences:', error);
    }
  }

  // Obtenir une prfrence
  getPreference(key) {
    return this.preferences[key];
  }

  // Dfinir une prfrence
  setPreference(key, value) {
    this.preferences[key] = value;
    this.savePreferences();
    this.applyPreferences();
  }

  // Obtenir toutes les prfrences
  getAllPreferences() {
    return { ...this.preferences };
  }

  // Appliquer les prfrences
  applyPreferences() {
    // Appliquer le thme
    this.applyTheme();
    
    // Appliquer la langue
    this.applyLanguage();
  }

  // Appliquer le thme
  applyTheme() {
    const { theme, autoTheme } = this.preferences;
    
    if (autoTheme) {
      // Dtecter automatiquement le thme systme
      const systemTheme = window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light';
      document.documentElement.setAttribute('data-theme', systemTheme);
    } else {
      document.documentElement.setAttribute('data-theme', theme);
    }
  }

  // Appliquer la langue
  applyLanguage() {
    const { language } = this.preferences;
    document.documentElement.setAttribute('lang', language);
    
    // mettre un vnement pour notifier les composants
    window.dispatchEvent(new CustomEvent('languageChanged', { detail: { language } }));
  }

  // couter les changements de thme systme
  listenToSystemTheme() {
    const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
    
    mediaQuery.addEventListener('change', (e) => {
      if (this.preferences.autoTheme) {
        this.applyTheme();
      }
    });
  }

  // Initialiser le service
  init() {
    this.applyPreferences();
    this.listenToSystemTheme();
  }
}

// Instance singleton
const preferencesService = new PreferencesService();

export default preferencesService;
